.main-chat-content {
  display: flex;
  width: 100%;
  height: 100%;

  &__chat-list {
    width: calc(25% - 1px);
    border-right: 1px solid #4a4a4a;

    &__h2 {
      font-weight: normal;
      color: aqua;
      padding: 0 20px;
    }
  }

  &__conversation {
    width: 75%;
  }
}